.header-container{
    position: fixed;
    width: 100vw;
    height:15vh;
    background-color: coral;
    top:0;
    left: 0;
    z-index: 10000;
}

.header-container > div{
    width: 100%;
    height: 100%;
}

.header-container > div > div{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.header-container > div > div span{
    position:absolute;
    top: calc(50% - 3vh);
    left: calc(15px + var(--span-index)*40px);
    display: inline-block;
    width: 36px;
    height: 6vh;
    background-color: black;
    border-radius: 5px;
    color: blueviolet;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 6vh;
    text-align: center;
    transform: translate(0%,0%);
    opacity: 0;
}

.header-container > div > div span:nth-of-type(8){
    visibility: hidden;
}

.header-container > div > div span.anim{
    animation: textAnim 2s ease-in-out calc(var(--span-index)*0.1s) forwards ;
} 

.header-container > div > div span.fadeout{
    animation: textFadout 1s ;
} 

@keyframes textAnim{
    0%{
        transform: translate(2000%,200%);
        opacity: 0
    }
    100%{
        transform: translate(0%,0%);
        opacity: 1
    }
}

@keyframes textFadout{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }   
}